import React from "react"
import { HashRouter, Route } from "react-router-dom"; /*{ÎùºÏö∞ÌåÖÏùÑ ÏúÑÌïú Ïª¥Ìè¨ÎÑåÌä∏, npm install react-router-dom }*/ 
import Home from "./routes/Home"
import About from "./routes/About"
import Detail from "./routes/Detail"
import Navigation from "./components/Navigation";
import "./App.css"

function App(){
  return ( 
  <HashRouter>
    <Navigation />
    <Route path="/" exact={true} component={Home} />  {/*exact : Ï£ºÏñ¥ÏßÑ Í≤ΩÎ°úÏôÄ Ï†ïÌôïÌûà ÎßûÏïÑ Îñ®Ïñ¥Ï†∏ÏïºÎßå ÎùºÏö∞ÌåÖ */}
    <Route path="/about" component={About} />        
    <Route path="/movie/:id" component={Detail} />
  </HashRouter>
  );
}

export default App; 